<template>
  <BaseLayout title="商品详情" bg="#EEE" notitle>

    <div v-if="state.detail">

      <van-swipe class="image" :autoplay="3000" lazy-render>
        <van-swipe-item v-for="image in state.detail.image" :key="image">
          <van-image :src="image" width="100%" fit="contain" />
        </van-swipe-item>
      </van-swipe>

      <div class="group">
        <van-cell center :border="false">
          <template #title><span class="price"><span class="symbol">￥</span>{{state.price}}</span></template>
          <template #right-icon><van-icon :name="state.like ? 'like' : 'like-o'" size="24" :color="state.like ? '#F00' : '#000'" @click.stop="like(id)" /></template>
        </van-cell>
        <van-cell class="title" :border="false">
          <template #title>{{state.detail.title}}</template>
          <template #label><van-tag type="danger" v-show="state.detail.isSelf">京东自营</van-tag> &nbsp; {{state.detail.seoModel}} / {{state.detail.wareQD}} / {{state.detail.wserve}} </template>
        </van-cell>
      </div>

      <div class="group">
        <van-cell v-for="(v,k) in state.spec" :key="k" :title="v.saleName" :border="true">
          <template #label>
            <div class="spec" v-for="(item,index) in v.saleAttrList" :key="index" :class="{active: item.skuIds[0] == id}" @click="change(item.skuIds[0])">
              <van-icon v-if="k == 0" :name="'//img13.360buyimg.com/n4/' + item.imagePath" size="24"></van-icon>
              &nbsp;{{item.saleValue}}
            </div>
          </template>
        </van-cell>
        <van-cell title="数量">
          <van-stepper v-model="state.num" theme="round" button-size="22" :min="1"/>
        </van-cell>
      </div>

      <div class="group">
        <van-cell title="参数" :value="state.detail.weight + ' kg / ' + state.detail.saleUnit " is-link @click="state.popup=true"></van-cell>
        <van-cell is-link :to="'/my/address/change?target=/jdmall/item?id=' + id">
          <template #title>
            送至：{{address}}
          </template>
          <template #label>
           <van-tag type="warning" v-show="state.detail.isJDLogistics">京东物流</van-tag>
           &nbsp;<span v-html="state.predict"></span>
          </template>
        </van-cell>
      </div>

      <div class="detail" v-html="state.detail.introduction"></div>

      <van-action-sheet v-model:show="state.popup" title="参数">
        <van-cell v-for="(v,k) in state.detail.paramJson" :key="k" :title="v['name']" :value="v['value']"></van-cell>
      </van-action-sheet>

      <van-action-bar>
        <van-action-bar-icon icon="wap-home-o" text="首页" @click="$router.push('/')" />
        <van-action-bar-icon icon="balance-list-o" text="订单" @click="$router.push('/my/order')" />
        <van-action-bar-icon icon="cart-o" :icon-class="state.animate" text="购物车" :badge="profile && profile.cartNumber ? profile.cartNumber : ''" @click="$router.push('cart')" />
        <van-action-bar-button type="danger" :disabled="!state.stock || !state.permit" :text="button" @click="addCart" />
      </van-action-bar>

    </div>

    <van-empty v-else image="error" description="商品不存在或已下架"></van-empty>

  </BaseLayout>
</template>

<style scoped>
.image {
  width: 100%;
  height: calc(100vw);
  padding: 0px;
  background: #FFF;
  overflow: hidden;
}
.group {
  margin-top: 8px;
}
.price {
  color: #F00;
  font-size: 24px;
  font-weight: 600;
}
.symbol {  
  font-size: 14px;
}
.title {
  font-size: 18px;
  line-height: 28px;
}

.detail {
  width: 100%;
  margin: 18px 0 0 0;
  padding: 0 0 100px 0;
  min-height: 200px;
  background: #FFF;
  overflow-x: hidden;
  zoom: 50%;
}
.detail img {
  width: 100%;
}

.spec {
  float: left;
  min-width: 64px;
  height: 32px;
  margin: 3px;
  padding: 2px;
  border: solid 1px #CCC;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}
.spec.active {
  background: #FEE;
  border-color: #ee0a24;
}

</style>

<script>
import {reactive} from 'vue'
import {mapState, mapGetters, mapMutations} from 'vuex'
import BaseLayout from '@/components/BaseLayout.vue'

export default {
  components: {BaseLayout},

  props: {
    type: {
      type: String,
      default: "mall",
    },
    id: {
      type: [String, Number],
      default: 1,
    },
  },

  data() {
    const state = reactive({
      // 详情
      detail: {},

      // 喜欢
      like: false,

      // 参数显示
      popup: false,

      // 规格 和 数量
      spec: [],
      num: 1,

      // 配送预估
      predict: "",

      // 价格、是否有库存、是否允许购买
      price: 0,
      stock: 0,
      permit: 0,

      // 购物车动画
      animate: "",
    });

    return {state};
  },

  created() {
    this.init(this.id);
  },

  mounted() {
    this.toggleTabbar(false);
  },

  unmounted() {
    this.toggleTabbar(false);
  },

  computed: {
    button (){
      if (this.state.price== 0)
        return "正在查询 ...";
      if (!this.state.stock)
        return "库存不足";
      if (!this.state.permit)
        return "商品未上架";
      return "加入购物车";
    },
    ...mapState(["profile"]),
    ...mapGetters(["authorized", "address", "addressCode"]),
  },

  methods: {
    init(id) {
      this.state.price = 0;
      this.state.stock = 0;
      this.state.permit = 0;

      this.$http.post("/jdmall/item?id=" + id).then((data) => {
        this.state.detail = _.isEmpty(data) ? null : {...data, ...{
          image: (data.image.length <= 5 ? [data.cover] : data.image.split(",")),
        }};
      });

      this.$http.get("/jdmall/query?method=product/getSimilarSku&skuId=" + id).then((data) => {
        this.state.spec = data;
      });

      this.$http.get("/jdmall/itemPredict?id=" + id + "&address=" + this.addressCode).then((data) => {
        this.state.predict = data.predict;
      });
      
      this.$http.get("/jdmall/itemCheck?id=" + id + "&num=1&address=" + this.addressCode).then((data) => {
        this.state = {...this.state, ...data["_" + id]};
      });

      this.authorized && this.$http.get("/mall/interestList?id=" + id).then((data) => {
        this.state.like = !_.isEmpty(data);
      });
    },

    like(id) {
      this.$http.post("/mall/interestSave?type=jdmall&id=" + id + "&remove=" + (this.state.like ? "1" : "0")).then(() => {
        this.state.like = !this.state.like;
      });
    },

    change(id) {
      if (this.id == id) return;
      this.$router.replace('?id=' + id);
      this.init(id);
    },

    addCart(){
      this.$http.post("/mall/cartSave?spu=" + this.id + "&sku=" + this.id + "&num=" + this.state.num + "&status=1&type=jdmall").then((data) => {
        this.cartNumberAdd(1);
        this.state.animate = "animate__animated animate__backInDown";
        setTimeout(() => {
          this.state.animate = "";
        }, 1000);
      });
    },

    ...mapMutations(["togglePopup","toggleTabbar","cartNumberAdd"]),
  }
}
</script>
